import React from "react";
import PropTypes from "prop-types";
import { Accordion } from "react-bootstrap";
import FaqList from "../../components/faq";
import Button from "../../components/shared/button";
import {
    CCarousel,
    CCarouselItem,
    CImage,
    CCarouselCaption,
} from "@coreui/react";
import data from "../../data/digital-marketing/data.json";

export default function DigitalMarket() {
    return (
        <>
            <div className="services-page-container-1 content-writer">
                <div className=" container">
                    <div className="api-types-accordion content-writer-section">
                        <h2 className="">{data.pagetitle}</h2>
                    </div>
                </div>
            </div>

            <div className="services-page-container-2 ">
                <div className="container">
                    <div className="get-service-section">
                        <h5 className="">{data.subtitle}</h5>
                        <h2 className="services-page-subtitle">{data.title}</h2>
                        <p>{data.description}</p>

                        <div className="services-page-section">
                            <ul className="">
                                {data.list.map((items, i) => {
                                    return (
                                        <li key={i.id}>
                                            <i className=" fas fa-check"></i>
                                            {items.item}
                                        </li>
                                    );
                                })}
                            </ul>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container">
                <div className="website-container content-in-mind-section container">
                    <h2 className="web-subtitle">{data.section3title}</h2>
                    <p>{data.section3description}</p>

                    <div className="affordable-web-btn">
                        <Button
                            size="medium"
                            path={`/contact-us`}
                            className="button-started"
                        >
                            {data.buttonText}
                        </Button>
                    </div>
                </div>
            </div>

            <div className="digital-marketing-contianer-outer">
                <div className="package-plan-section-outer container d-flex flex-row flex-wrap">
                    <div className="package-plan-col col-lg-3 col-md-3">
                        <div className="">
                            <div className="">
                                <div className="package-plan-col-title">
                                    <div className="">
                                        <div className="package-plan-col-title-wrap">
                                            <h3 className="package-plan-col-title">
                                                {data.basicpackage}
                                            </h3>
                                        </div>
                                    </div>
                                </div>

                                <div className="package-plan-col-block digi-mar-sec-cols">
                                    <div className="">
                                        <span className="price-symbol">
                                            {data.symbol}
                                        </span>
                                        <div className="wpr-pricing-table-badge-inner hidden">
                                            Most Popular
                                        </div>
                                        <span className="package-price">
                                            {data.basicpackagePrice}
                                        </span>
                                    </div>

                                    <div className="col-month-div">
                                        {data.duration}
                                    </div>
                                </div>

                                {data.basicdetails.map((items, i) => {
                                    return (
                                        <section className="" key={i}>
                                            <div>
                                                <i className="fas fa-check"></i>

                                                <span>
                                                    <span>{items.item}</span>
                                                </span>
                                            </div>
                                        </section>
                                    );
                                })}

                                <div>
                                    <Button
                                        size="medium"
                                        path={`/services/`}
                                        className="button-started digital-marketing"
                                    >
                                        {data.button}
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="package-plan-col col-lg-3 col-md-3">
                        <div className="">
                            <div className="">
                                <div className="-6ce1609 ">
                                    <div>
                                        <div className="package-plan-col-title-wrap">
                                            <h3 className="package-plan-col-title">
                                                {data.standardpackage}
                                            </h3>
                                        </div>
                                    </div>
                                </div>

                                <div className="package-plan-col-block digi-mar-sec-cols">
                                    <div>
                                        <span className="price-symbol">
                                            {data.symbol}
                                        </span>
                                        <div className="wpr-pricing-table-badge-inner hidden">
                                            Most Popular
                                        </div>
                                        <span className="package-price">
                                            {" "}
                                            {data.standardpackagePrice}
                                        </span>
                                    </div>

                                    <div className="col-month-div">
                                        {data.duration}
                                    </div>
                                </div>
                                {data.standarddetails.map((items, i) => {
                                    return (
                                        <section className=" " key={i}>
                                            <div>
                                                <i className="fas fa-check"></i>

                                                <span>
                                                    <span>{items.item}</span>
                                                </span>
                                            </div>
                                        </section>
                                    );
                                })}

                                <div className="-26c9e99  n">
                                    <Button
                                        size="medium"
                                        path={`/services/`}
                                        className="button-started digital-marketing"
                                    >
                                        {data.button}
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="package-plan-col col-lg-3 col-md-3">
                        <div className="">
                            <div>
                                <div>
                                    <div>
                                        <div className="package-plan-col-title-wrap">
                                            <h3 className="package-plan-col-title">
                                                {data.premiumpackage}
                                            </h3>
                                        </div>
                                    </div>
                                </div>

                                <div className="package-plan-col-block digi-mar-sec-cols">
                                    <div>
                                        <span className="price-symbol">
                                            {data.symbol}
                                        </span>
                                        <div className="wpr-pricing-table-badge-inner">
                                            Most Popular
                                        </div>
                                        <span className="package-price">
                                            {" "}
                                            {data.premiumpackagePrice}
                                        </span>
                                    </div>

                                    <div className="col-month-div">
                                        {data.duration}
                                    </div>
                                </div>

                                {data.premiumdetails.map((items, i) => {
                                    return (
                                        <section className="" key={i}>
                                            <div>
                                                <i className="fas fa-check"></i>

                                                <span>
                                                    <span>{items.item}</span>
                                                </span>
                                            </div>
                                        </section>
                                    );
                                })}

                                <div className="">
                                    <Button
                                        size="medium"
                                        path={`/services/`}
                                        className="button-started digital-marketing"
                                    >
                                        {data.button}
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="services-page-container-3 content-writer-sec">
                <div className="container">
                    <h5 className="content-writer-sec-title">
                        {data.section2subtitle}
                    </h5>
                    <h2 className="services-page-subtitle">
                        {data.section2title}
                    </h2>
                    <div className="row">
                        {data.blocks.map((blocks, i) => {
                            return (
                                <div
                                    key={i}
                                    className="col-md-4 col-sm-4 col-xs-6"
                                >
                                    <div className="card">
                                        <div className="card-body">
                                            <h5 className="card-title">
                                                {blocks.title}
                                            </h5>
                                            <p className="card-text">
                                                {blocks.description}
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>

            <div className="container ">
                <div className="best-affordable-price-section d-flex flex-wrap flex-row">
                    <div className="best-aff-price-sec-title">
                        <h2 className="web-subtitle">{data.section4title}</h2>
                    </div>

                    <div className="best-aff-price-sec-desc">
                        <ul className="app-dev-page-section content-writer-list">
                            {data.list4.map((items, i) => {
                                return (
                                    <li key={i.id}>
                                        <i className={items.iconClass}></i>
                                        {items.item}
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                </div>
            </div>

            <div className="services-page-container digital-marketing-section">
                <div className="container">
                    <h2 className="services-page-subtitle">
                        {data.section5title} <span>{data.section5span}</span>
                    </h2>
                    <p>{data.section5description}</p>

                    <div className="services-page-list">
                        <ul className="app-dev-page-section">
                            {data.list5.map((items, i) => {
                                return (
                                    <li key={i.id}>
                                        <i className=" fas fa-check"></i>
                                        {items.item}
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                </div>
            </div>

            <div className="slider-section container">
                <div className="container">
                    <CCarousel controls indicators>
                        {data.slider.map((itm, i) => {
                            return (
                                <CCarouselItem>
                                    <CCarouselCaption className="d-none d-md-block">
                                        <i className={itm.symbol}>
                                            {itm.rating}
                                        </i>
                                        <h5>{itm.Name}</h5>
                                        <p>{itm.description}</p>
                                        <span> {itm.span}</span>
                                    </CCarouselCaption>
                                </CCarouselItem>
                            );
                        })}
                    </CCarousel>
                </div>
            </div>

            <div className="services-page-container digital-marketing-section-2">
                <div className="container">
                    <div className="package-section digital-marketing-sec-acc">
                        <div className="container">
                            <h4 className="seo-page-title ">
                                {data.section6title}
                            </h4>
                            <p>{data.section6description}</p>

                            {data.accordion.map((accordionsItem, i) => {
                                return (
                                    <Accordion
                                        defaultactivekey={i}
                                        className="accordion-item"
                                    >
                                        <FaqList
                                            title={
                                                accordionsItem.accordionTitle
                                            }
                                            description={
                                                accordionsItem.accordionDescription
                                            }
                                        />
                                    </Accordion>
                                );
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
DigitalMarket.propTypes = {
    pagetitle: PropTypes.string,
    subtitle: PropTypes.string,
    title: PropTypes.string,
    description: PropTypes.string,
    item: PropTypes.string,
    section2title: PropTypes.string,
    blocks: PropTypes.string,
    section3title: PropTypes.string,
    section3description: PropTypes.string,
    buttonText: PropTypes.string,
    section4title: PropTypes.string,
    list4: PropTypes.string,
    list4: PropTypes.string,
    section5title: PropTypes.string,
    section5span: PropTypes.string,
    section5description: PropTypes.string,
    section6title: PropTypes.string,
    section6description: PropTypes.string,
    accordionTitle: PropTypes.string,
    accordionDescription: PropTypes.string,
    symbol: PropTypes.string,
    duration: PropTypes.string,
    button: PropTypes.string,
    package: PropTypes.string,
    packagePrice: PropTypes.string,
};
